import { Component,Inject} from '@angular/core';
import { JsonCreate } from 'src/app/services/jsonCreate.service';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { MatDialog,MatDialogRef,MAT_DIALOG_DATA} from '@angular/material/dialog';


export interface DialogData {
  collections: string;
  name: string;
  id: string
  js: any
  
}

@Component({
  selector: 'app-dnevnik-read',
  animations: [
    trigger('changeDivSize',[
      state('initial', style({
      })),
      state('final', style({
      })),
      transition('initial<=>final', animate('200ms')),
      transition('final=>initial', animate('200ms'))
      
    ])
  ],
  templateUrl: './dnevnik-read.component.html',
  styleUrls: ['./dnevnik-read.component.sass'],
  
})
export class DnevnikReadComponent  {
  jsonData: any
  selectCollection: any

  collections: [string];
  name: string;
  all = "all"
  col
  array = []
  select= "all"
  
  // isClick = false

  constructor(private js: JsonCreate, public dialog: MatDialog) {
    this.js.getConfig().subscribe(value => {
      this.jsonData = value
      this.selectCollection = value
      
      for (let i = 0; i < this.jsonData.length; i++) {
        console.log(this.jsonData[i].collection)
        this.array.push(this.jsonData[i].collection)
        
        
      }
      this.col = new Set(this.array)
      this.col.delete("")
      console.log(value)
      console.log(this.col)
    })

    
  }
  openDialog(i: number): void {
    console.log(this.collections)
    console.log(this.jsonData[i].message)
    const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
      width: '450px',
      data: { 
        name: this.jsonData[i].title,
        collections: this.jsonData[i].message,
        id: this.jsonData[i]._id,
        js: this.js
      }
        
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.DisplaySelectedRecord(this.select)
    });
    


  }


  DisplaySelectedRecord(selectedCollection: string){
    this.js.getSelectedConfig(selectedCollection).subscribe(value=> {
      this.selectCollection = value
      this.select = selectedCollection
      console.log(this.selectCollection)
    })

  }

  




    

}

@Component({
  selector: 'dialog-data-example-dialog',
  templateUrl: 'dialog-data-example-dialog.html',
  styleUrls: ['dialog-data-example-dialog.sass']
 })

 export class DialogOverviewExampleDialog {  
  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
  
    @Inject(MAT_DIALOG_DATA) public data: DialogData,) {}

    onNoClick(): void {
      this.dialogRef.close();
    }

    deleteElement(id){
      this.data.js.deleteSelectedElement(id).subscribe(value => {
        console.log(value)
      })
    }
    
      
    

  
 }
