import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpHeaders } from '@angular/common/http';

const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json',
    })
  };

@Injectable()



export class JsonCreate {


constructor(private http: HttpClient) { }

configUrl = 'https://jsonbox.io/box_ce820a7b8b65281a8753';
configCollections ='https://jsonbox.io/box_ce820a7b8b65281a8753?q=collection:'
configDeleteAll = "https://jsonbox.io/box_f9ba3926dade96693495?q=message:**"//удалить все
configDelete = 'https://jsonbox.io/box_ce820a7b8b65281a8753/'
getConfig() {
  return this.http.get(this.configUrl);
}

getSelectedConfig(selectedConfig) {
  console.log(selectedConfig)
  if(selectedConfig === "all"){
    return this.http.get(this.configUrl)
  }
  else return this.http.get(this.configCollections+selectedConfig)
}

postJson(title,message,collection) {
    const data ={
      title,
      message,
      collection

    }
    console.log(data)
    return this.http.post(this.configUrl, data, httpOptions)
}

deleteData() {
  return this.http.delete(this.configDeleteAll)
}
deleteSelectedElement(id: string) {
  console.log(this.configDelete+id)
  return this.http.delete(this.configDelete+id)
}


}

